.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.overlayOpen {
  opacity: 1;
  visibility: visible;
}

.mobileMenu {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 280px;
  max-width: 80vw;
  background: white;
  box-shadow: -2px 0 8px 0px #00000040;
  display: flex;
  flex-direction: column;
  padding: 24px;
  gap: 16px;
  z-index: 1000;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  overflow-y: auto;
}

.closeButton {
  all: unset;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  cursor: pointer;
  align-self: flex-end;
  margin-bottom: 8px;
  transition: transform 0.2s ease;

  &:hover {
    transform: scale(1.1);
  }
}

.closeIcon {
  font-size: 32px;
  font-weight: 300;
  line-height: 1;
  color: #ef8e01;
  user-select: none;
}

.mobileMenuOpen {
  transform: translateX(0);
}

.mobileMenuButton {
  all: unset;
  color: #0D2344;
  font-size: 18px;
  cursor: pointer;
  padding: 12px 0;
  text-align: left;
  transition: color 0.2s ease;
  border-bottom: 1px solid #EFEEEC;

  &:hover {
    color: #ef8e01;
  }

  &:last-child {
    border-bottom: none;
  }
}

