.header {
position: absolute;
  display: flex;
  align-items: center;

  left: 50%;
  transform: translateX(-50%);

  width: 80%;
  max-width: 1330px;
  height: 110px;

  margin: 0 auto;
  border-bottom-right-radius: 15px;

  background: white;
  box-shadow: 0px 4px 4px 0px #00000040;

  border-bottom-left-radius: 15px;
  padding: 12px 24px 36px 24px;
  z-index: 1000;
}

.navigation {
  width: 100%;
  display: flex;
  align-items: end;
  justify-content: center;
}

.desktopNav {
  display: flex;
  align-items: end;
  justify-content: center;
  width: 100%;
}

.mobileNav {
  display: none;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.logotype {
  margin: 0 64px;
  transform: translateY(-4px);
  flex-shrink: 0;
}

.navigationButton {
  all: unset;
  color: #0D2344;
  font-size: 16px;
  cursor: pointer;
  transition: color 0.2s ease;

  &:hover {
    color: #ef8e01;
  }
}

.navigationButton + .navigationButton {
  margin-left: 28px;
}

@media (max-width: 1048px) {
  .logotype {
    margin: 0 48px;
  }

  .navigationButton + .navigationButton {
    margin-left: 16px;
  }
}

@media (max-width: 976px) {
  .logotype {
    display: none;
  }

  .header {
    height: 100px;
    padding: 12px 24px 12px 24px;
  }

  .navigation {
    gap: 16px;
  }

  .navigationButton + .navigationButton {
    margin-left: 0;
  }
}

.mobileLogotype {
  flex-shrink: 0;
}

@media (max-width: 700px) {
  .header {
    width: 100%;
    border-radius: 0;
    position: relative;
    padding: 16px 24px;
    height: auto;
    min-height: 80px;
  }

  .desktopNav {
    display: none;
  }

  .mobileNav {
    display: flex;
  }

  .navigation {
    align-items: center;
    justify-content: space-between;
  }
}