.hamburgerButton {
  all: unset;
  display: flex;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
  padding: 8px;
  z-index: 1001;
}

.hamburgerLine {
  width: 24px;
  height: 3px;
  background-color: #ef8e01;
  border-radius: 2px;
  transition: all 0.3s ease;
}

.open {
  .hamburgerLine {
    &:nth-child(1) {
      transform: rotate(45deg) translate(6px, 6px);
    }

    &:nth-child(2) {
      opacity: 0;
      transform: translateX(-10px);
    }

    &:nth-child(3) {
      transform: rotate(-45deg) translate(6px, -6px);
    }
  }
}

